.card {
  min-height: 320px;
  margin-bottom: 20px;
  background-color: #f5f6f7;
}
.card-images__wrap {
  min-width: 400px;
  max-width: 400px;
  padding: 0;
}

.card-info__wrap {
  max-width: 200px;
  padding: 0;
}

@media (max-width: 1200px) {
  .card-info__wrap {
    max-width: 160px;
    padding: 0;
  } 
}

@media (max-width: 992px) {
  .card-images__wrap {
    min-width: 240px;
    max-width: 240px;
  }
  .card-info__wrap {
    max-width: 120px;
    position: absolute;
    top: 0;
    right: 0;
  }
}

@media (max-width: 768px) {
  .card-images__wrap {
    min-width: 100%;
    max-width: 100%;
  }
}



