.card-info {
  background-color: #e6e6e7;
  min-height: 320px;
}

.card-price {
  background-color: #1c374a;
  text-align: right;
  padding: 20px 24px;
  min-height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.card-price__text-line {
  text-decoration: line-through;
}

.card-price__tax,
.card-price__text-line {
  color: #9b9b9b;
  font-size: 18px;
  line-height: 1;
}

.card-price__currency {
  font-size: 58px;
  line-height: 1;
  color: #fff;
}

.card-price__currency-sign {
  font-size: 26px;
  vertical-align: super;
}

@media (max-width: 992px) {
  .card-price__tax,
  .card-price__text-line {
    font-size: 16px;
  }
  .card-price {
    padding: 10px 15px;
    min-height: 85px;
  }
  .card-price__currency {
    font-size: 40px;
  }
  .card-price__currency-sign {
    font-size: 20px;
  }
  .card-info {
    background: none;
  } 
}




