.card-images {
  position: relative;
  overflow: hidden;
}

.card-img-small {
  position: absolute;
  width: 200px;
  top: 0;
  right: -50px;
  z-index: 5;
}

@media (max-width: 992px) {
  .card-img {
    display: block;
    width: 400px;
  }
  .card-img-small {
    display: none;
  } 
}


@media (max-width: 768px) {
  .card-img {
    width: 100%;
  }
}




