.card-permission {
  padding: 10px;
}

.card-permission__text {
  text-transform: capitalize;
}

.card-permission__icon {
  display: inline-block;
  vertical-align: middle;
  width: 30px;
  height: 24px;
  background-image: url('../../../../../../assets/meta/511_pet-friendly.svg');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50%;
}

.pet {
  background-image: url('../../../../../../assets/Shape.svg');
}

.kid {
  background-image: url('../../../../../../assets/meta/501_kid-friendly.svg');  
}

.senior {
  background-image: url('../../../../../../assets/meta/510_senior-friendly.svg');  
}

.romantic {
  background-image: url('../../../../../../assets/meta/504_romantic.svg');  
}

.card-permission__text {
  margin-left: 15px;
}

@media (max-width: 992px) {
  .card-permission {
    display: none;
  } 
}




