.card-descr {
  padding: 10px 0;
}

.rating {
  margin-bottom: 20px;
}

.rating-stars,
.rating-reviews,
.rating-hours {
  display: inline-block;
  white-space: nowrap;
  margin-right: 20px;
}

.rating-hours {
  padding-left: 25px;
  color: #979797;
  background-image: url('../../../../assets/duration.svg');
  background-repeat: no-repeat;
}

.description {
  margin-bottom: 40px;
  font-family: arial, sans-serif !important;
}

.review-text {
  margin-top: 5px;
  background-image: url('../../../../assets/review.png');
  background-repeat: no-repeat;
  padding-left: 35px;
  word-spacing: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 992px) {
  .rating,
  .card-descr__title {
    width: 80%;
    padding-right: 90px;
  }
}


@media (max-width: 768px) {
  .rating,
  .card-descr__title {
    width: 100%;
    padding-right: 0;
  }
}




